/* src/pages/Memo.css */
input, textarea {
    width: calc(100% - 1rem - 5px);
    margin-bottom: 10px;
    padding: .5rem;
}

button {
    padding: .1rem .5rem;
    cursor: pointer;
}

ul {
    list-style-type: none;
    padding: 0;
}

li {
    background: #ddd;
    margin-bottom: 10px;
    padding: 10px;
}

article {
    margin: .5rem;
    padding: .5rem;
    border-radius: .5rem;
    background-color: #eee;
}

h1, h2, h3, h4, h5, h6 {
    margin: .5rem 0;
}

.navbar {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    padding: 10px;
    background-color: #f0f0f0;
    border-bottom: 1px solid #ccc;
}

.menu-button {
    background: none;
    border: none;
    cursor: pointer;
    padding: 5px;
    margin-right: 10px;
}

.dropdown {
    position: absolute;
    top: 50px;
    left: 10px;
    background-color: white;
    border: 1px solid #ccc;
    border-radius: 6px;
    box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.1);
    padding: 10px;
    z-index: 1000;
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.markdown-preview {
    background-color: #fafafa;
    border: 1px solid #ddd;
    padding: 1rem;
    border-radius: 6px;
    max-height: 300px;
    overflow: auto;
}

.markdown-preview pre {
    background: #f6f8fa;
    padding: 0.5rem;
    border-radius: 4px;
    overflow: auto;
}